/* ANCHOR Project List */

#header {
  text-align: center;
  font-size: 3rem;
  line-height: 10rem;
  height: 10rem;
  background-color: #c2c2c2;
  letter-spacing: 5px;
  box-shadow: 0 0 10px;
  border: 1px solid #bbbbbb;
}

#projects {
  display: flex;
  background-image: url("./images/ProjectBackground.jpeg");

  min-height: 100vh;
  
  background-attachment: fixed;
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;

  
}

.MuiList-root {
  width: 300px;
  min-width: 300px;
  background-color: #d1d1d1;
  height: 100vh;
  max-height: 100vh;
  color: black;
  opacity: 0.92;
}

#dropdown {
  background-color: #a5a5a5;
  position: absolute;
  z-index: 1;
  right:-1.5rem;
  box-shadow: 0 0 3px;
  top: 50%;
}

#project-img-preview {
  height: 27rem;
  width: 32rem;
  object-fit: cover;
  border-radius: 20px;
  box-shadow: 0 0 10px;
  margin: auto 5% auto 5rem;
}

#project-desc {
  width: 31rem;
  height: 27rem;
  margin: auto 0 auto 0;
  background-color: #dadada;
  border-radius: 20px;
  box-shadow: 0 0 10px;

}

/* ANCHOR Experience */
#experience {
  /* display: flex; */
  background-image: url("./images/ExperienceBackground.jpg");

  min-height: 100vh;
  
  background-attachment: fixed;
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
}


